import React from "react";
import { Link as DomLink } from "react-router-dom";

export interface LinkProps {
  to?: string;
  label: string;
}

function Link({ to = "/", label = "mink" }: LinkProps) {
  return (
    <div className="link">
      <DomLink to={to}>{label}</DomLink>
    </div>
  );
}

export default Link;
